        }
      }

      return res;
    },
    // make suggestions align right when there are not enough items
    alignRight: function alignRight() {
      var navCount = (this.$site.themeConfig.nav || []).length;
      var repo = this.$site.repo ? 1 : 0;
      return navCount + repo <= 2;
    }
  },
